'use client'

import { useState } from 'react'
import { useParams, useRouter } from 'next/navigation'
import Link from 'next/link'

// Types
type Message = {
  id: string
  author: {
    name: string
    role: 'Customer' | 'Admin' | 'Support Agent'
  }
  message: string
  timestamp: string
  isInternal?: boolean
}

type Ticket = {
  id: string
  subject: string
  description: string
  status: 'Open' | 'In Progress' | 'Resolved' | 'Closed'
  priority: 'Low' | 'Medium' | 'High' | 'Urgent'
  category: string
  companyId: string
  companyName: string
  subAccountId?: string
  subAccountName?: string
  submitter: {
    name: string
    email: string
    phone: string
  }
  assignedTo?: {
    name: string
    email: string
  }
  messages: Message[]
  createdAt: string
  updatedAt: string
  resolvedAt?: string
  closedAt?: string
}

// Mock Data
const mockTicket: Ticket = {
  id: 'tick-001',
  subject: 'Unable to access dashboard',
  description: 'Getting 404 error when trying to access the main dashboard. This started happening after the latest update yesterday. I have tried clearing browser cache and using different browsers (Chrome, Firefox) but the issue persists.',
  status: 'Open',
  priority: 'High',
  category: 'Technical',
  companyId: 'comp-001',
  companyName: 'Tech Solutions Inc.',
  subAccountId: 'sub-001',
  subAccountName: 'Sales Team',
  submitter: {
    name: 'Mike Davis',
    email: 'mike@techsolutions.com',
    phone: '+1-555-0103'
  },
  messages: [
    {
      id: 'msg-001',
      author: { name: 'Mike Davis', role: 'Customer' },
      message: 'Getting 404 error when trying to access the main dashboard. This started happening after the latest update yesterday.',
      timestamp: '2024-12-08 09:15'
    },
    {
      id: 'msg-002',
      author: { name: 'Support Team', role: 'Support Agent' },
      message: 'Thank you for reporting this issue. We are investigating and will get back to you shortly. Can you please provide your browser version and any error messages you see in the console?',
      timestamp: '2024-12-08 09:30'
    },
    {
      id: 'msg-003',
      author: { name: 'Admin Team', role: 'Admin' },
      message: 'Internal note: This appears to be related to the routing update. Checking the logs now.',
      timestamp: '2024-12-08 10:00',
      isInternal: true
    },
  ],
  createdAt: '2024-12-08 09:15',
  updatedAt: '2024-12-08 10:00'
}

export default function TicketDetailPage() {
  const params = useParams()
  const router = useRouter()
  const ticketId = params.id as string

  const [ticket, setTicket] = useState<Ticket>(mockTicket)
  const [replyMessage, setReplyMessage] = useState('')
  const [isInternal, setIsInternal] = useState(false)
  const [isEditing, setIsEditing] = useState(false)
  const [editedTicket, setEditedTicket] = useState<Ticket>(mockTicket)
  const [showInternalNotes, setShowInternalNotes] = useState(true)

  const handleSave = () => {
    setTicket(editedTicket)
    setIsEditing(false)
  }

  const handleCancel = () => {
    setEditedTicket(ticket)
    setIsEditing(false)
  }

  const handleSendReply = () => {
    if (!replyMessage.trim()) return

    const newMessage: Message = {
      id: `msg-${Date.now()}`,
      author: { name: 'Admin Team', role: isInternal ? 'Admin' : 'Support Agent' },
      message: replyMessage,
      timestamp: new Date().toLocaleString('en-US', { 
        year: 'numeric', 
        month: '2-digit', 
        day: '2-digit', 
        hour: '2-digit', 
        minute: '2-digit' 
      }),
      isInternal
    }

    setTicket(prev => ({
      ...prev,
      messages: [...prev.messages, newMessage],
      updatedAt: newMessage.timestamp
    }))

    setReplyMessage('')
    setIsInternal(false)
  }

  const handleInputChange = (field: string, value: any) => {
    setEditedTicket(prev => ({ ...prev, [field]: value }))
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Open': return 'bg-blue-100 text-blue-800'
      case 'In Progress': return 'bg-yellow-100 text-yellow-800'
      case 'Resolved': return 'bg-green-100 text-green-800'
      case 'Closed': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'Urgent': return 'bg-red-100 text-red-800'
      case 'High': return 'bg-orange-100 text-orange-800'
      case 'Medium': return 'bg-yellow-100 text-yellow-800'
      case 'Low': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  // Filter messages based on internal notes toggle
  const displayedMessages = showInternalNotes 
    ? ticket.messages 
    : ticket.messages.filter(msg => !msg.isInternal)

  return (
    <div className="p-6">
      {/* Header */}
      <div className="mb-6">
        <div className="flex items-center gap-3 mb-4">
          <Link href="/admin/support" className="text-purple-600 hover:text-purple-700">
            ‚Üê Back to Tickets
          </Link>
        </div>

        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-2 mb-2">
              {isEditing ? (
                <input
                  type="text"
                  value={editedTicket.subject}
                  onChange={(e) => handleInputChange('subject', e.target.value)}
                  className="border border-gray-300 rounded-lg px-3 py-1 text-2xl"
                />
              ) : (
                ticket.subject
              )}
            </h1>
            <div className="flex items-center gap-3">
              <span className={`px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(ticket.status)}`}>
                {ticket.status}
              </span>
              <span className={`px-3 py-1 rounded-full text-sm font-medium ${getPriorityColor(ticket.priority)}`}>
                {ticket.priority}
              </span>
              <span className="px-3 py-1 bg-purple-100 text-purple-800 rounded-full text-sm font-medium">
                {ticket.category}
              </span>
            </div>
            <p className="text-gray-600 mt-2">
              Ticket ID: {ticket.id} ‚Ä¢ Created: {ticket.createdAt}
            </p>
          </div>

          <div className="flex gap-2">
            {isEditing ? (
              <>
                <button onClick={handleSave} className="bg-green-600 text-white px-6 py-2 rounded-lg hover:bg-green-700">
                  üíæ Save
                </button>
                <button onClick={handleCancel} className="bg-gray-600 text-white px-6 py-2 rounded-lg hover:bg-gray-700">
                  ‚úñ Cancel
                </button>
              </>
            ) : (
              <>
                <button onClick={() => setIsEditing(true)} className="bg-blue-600 text-white px-6 py-2 rounded-lg hover:bg-blue-700">
                  ‚úèÔ∏è Edit
                </button>
                <button className="bg-red-600 text-white px-6 py-2 rounded-lg hover:bg-red-700">
                  üóëÔ∏è Delete
                </button>
              </>
            )}
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Main Content - Conversation Thread */}
        <div className="lg:col-span-2 space-y-6">
          {/* Original Description */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-2">Description</h3>
            {isEditing ? (
              <textarea
                value={editedTicket.description}
                onChange={(e) => handleInputChange('description', e.target.value)}
                rows={4}
                className="w-full px-4 py-2 border border-gray-300 rounded-lg"
              />
            ) : (
              <p className="text-gray-700 whitespace-pre-wrap">{ticket.description}</p>
            )}
          </div>

          {/* Conversation Thread */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-bold text-gray-900">Conversation ({ticket.messages.length})</h3>
              <label className="flex items-center gap-2 text-sm text-gray-600">
                <input
                  type="checkbox"
                  checked={showInternalNotes}
                  onChange={(e) => setShowInternalNotes(e.target.checked)}
                  className="w-4 h-4 text-purple-600 border-gray-300 rounded focus:ring-purple-500"
                />
                Show internal notes
              </label>
            </div>

            {/* Messages */}
            <div className="space-y-4">
              {displayedMessages.map((message) => (
                <div
                  key={message.id}
                  className={`p-4 rounded-lg ${
                    message.isInternal
                      ? 'bg-yellow-50 border border-yellow-200'
                      : message.author.role === 'Customer'
                      ? 'bg-blue-50 border border-blue-200'
                      : 'bg-gray-50 border border-gray-200'
                  }`}
                >
                  <div className="flex items-start justify-between mb-2">
                    <div className="flex items-center gap-2">
                      <div className="w-8 h-8 bg-purple-100 rounded-full flex items-center justify-center text-purple-600 font-bold text-sm">
                        {message.author.name.charAt(0)}
                      </div>
                      <div>
                        <div className="font-medium text-gray-900">{message.author.name}</div>
                        <div className="text-xs text-gray-500">{message.author.role}</div>
                      </div>
                    </div>
                    <div className="text-sm text-gray-500">{message.timestamp}</div>
                  </div>
                  <div className="ml-10 text-gray-700 whitespace-pre-wrap">{message.message}</div>
                  {message.isInternal && (
                    <div className="ml-10 mt-2">
                      <span className="px-2 py-1 bg-yellow-200 text-yellow-800 rounded-full text-xs font-medium">
                        üîí Internal Note
                      </span>
                    </div>
                  )}
                </div>
              ))}
            </div>

            {/* Reply Box */}
            <div className="mt-6 pt-6 border-t border-gray-200">
              <h4 className="font-semibold text-gray-900 mb-3">Reply to ticket</h4>
              <textarea
                value={replyMessage}
                onChange={(e) => setReplyMessage(e.target.value)}
                rows={4}
                placeholder="Type your reply here..."
                className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
              />
              <div className="flex items-center justify-between mt-3">
                <label className="flex items-center gap-2 text-sm text-gray-700">
                  <input
                    type="checkbox"
                    checked={isInternal}
                    onChange={(e) => setIsInternal(e.target.checked)}
                    className="w-4 h-4 text-purple-600 border-gray-300 rounded focus:ring-purple-500"
                  />
                  Internal note (not visible to customer)
                </label>
                <button
                  onClick={handleSendReply}
                  disabled={!replyMessage.trim()}
                  className="bg-purple-600 text-white px-6 py-2 rounded-lg hover:bg-purple-700 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  üì§ Send Reply
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Sidebar - Ticket Details */}
        <div className="space-y-6">
          {/* Status Management */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Status Management</h3>
            <div className="space-y-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Status</label>
                {isEditing ? (
                  <select
                    value={editedTicket.status}
                    onChange={(e) => handleInputChange('status', e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500"
                  >
                    <option value="Open">Open</option>
                    <option value="In Progress">In Progress</option>
                    <option value="Resolved">Resolved</option>
                    <option value="Closed">Closed</option>
                  </select>
                ) : (
                  <div className={`px-3 py-2 rounded-lg text-sm font-medium inline-block ${getStatusColor(ticket.status)}`}>
                    {ticket.status}
                  </div>
                )}
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Priority</label>
                {isEditing ? (
                  <select
                    value={editedTicket.priority}
                    onChange={(e) => handleInputChange('priority', e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500"
                  >
                    <option value="Low">Low</option>
                    <option value="Medium">Medium</option>
                    <option value="High">High</option>
                    <option value="Urgent">Urgent</option>
                  </select>
                ) : (
                  <div className={`px-3 py-2 rounded-lg text-sm font-medium inline-block ${getPriorityColor(ticket.priority)}`}>
                    {ticket.priority}
                  </div>
                )}
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Category</label>
                {isEditing ? (
                  <input
                    type="text"
                    value={editedTicket.category}
                    onChange={(e) => handleInputChange('category', e.target.value)}
                    className="w-full px-4 py-2 border border-gray-300 rounded-lg"
                  />
                ) : (
                  <div className="text-gray-900">{ticket.category}</div>
                )}
              </div>
            </div>
          </div>

          {/* Company & Submitter Info */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Ticket Information</h3>
            <div className="space-y-3">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Company</label>
                <Link href={`/admin/companies/${ticket.companyId}`} className="text-purple-600 hover:underline">
                  {ticket.companyName}
                </Link>
              </div>
              {ticket.subAccountName && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Sub-Account</label>
                  <Link href={`/admin/sub-accounts/${ticket.subAccountId}`} className="text-purple-600 hover:underline">
                    {ticket.subAccountName}
                  </Link>
                </div>
              )}
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">Submitter</label>
                <div className="text-gray-900">{ticket.submitter.name}</div>
                <div className="text-sm text-gray-500">{ticket.submitter.email}</div>
                <div className="text-sm text-gray-500">{ticket.submitter.phone}</div>
              </div>
              {ticket.assignedTo && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-1">Assigned To</label>
                  <div className="text-gray-900">{ticket.assignedTo.name}</div>
                  <div className="text-sm text-gray-500">{ticket.assignedTo.email}</div>
                </div>
              )}
            </div>
          </div>

          {/* Timestamps */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Timestamps</h3>
            <div className="space-y-2 text-sm">
              <div>
                <span className="font-medium text-gray-700">Created:</span>
                <span className="ml-2 text-gray-900">{ticket.createdAt}</span>
              </div>
              <div>
                <span className="font-medium text-gray-700">Last Updated:</span>
                <span className="ml-2 text-gray-900">{ticket.updatedAt}</span>
              </div>
              {ticket.resolvedAt && (
                <div>
                  <span className="font-medium text-gray-700">Resolved:</span>
                  <span className="ml-2 text-gray-900">{ticket.resolvedAt}</span>
                </div>
              )}
              {ticket.closedAt && (
                <div>
                  <span className="font-medium text-gray-700">Closed:</span>
                  <span className="ml-2 text-gray-900">{ticket.closedAt}</span>
                </div>
              )}
            </div>
          </div>

          {/* Quick Actions */}
          <div className="bg-white rounded-lg border border-gray-200 p-6">
            <h3 className="text-lg font-bold text-gray-900 mb-4">Quick Actions</h3>
            <div className="space-y-2">
              <button className="w-full px-4 py-2 bg-yellow-600 text-white rounded-lg hover:bg-yellow-700 text-sm">
                Assign to Me
              </button>
              <button className="w-full px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 text-sm">
                Mark as Resolved
              </button>
              <button className="w-full px-4 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 text-sm">
                Close Ticket
              </button>
              <button className="w-full px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 text-sm">
                Export to PDF
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
